﻿.page-main__gallery {
  padding: $mobile-padding;
}

.gallery {
  &__header {
    @extend %h2-crown;
  }

  &__item {
    padding-bottom: 32px;
  }

  &__images-wrapper {
    display: flex;
  }

  &__image {
    width: 131px;
    height: 131px;
    border-radius: 8px;
    margin-right: 20px;

    flex-grow: 1;

    &:last-child {
      margin-right: 0;
    }
  }

  &__caption {
    margin-top: 16px;
  }

  &__caption-link {
    color: $c-blue;
  }

  &__button {
    color: $c-blue;
    text-align: center;
    line-height: $button-height;

    display: block;

    height: $button-height;
    border: 2px solid $c-blue;
    border-radius: 8px;
  }
}

@media (min-width: $desktop-width) {
  .page-main__gallery {
    padding: $desktop-padding;
  }

  .gallery {
    &__header {
      margin-bottom: 0;
    }

    &__list {
      padding-top: 61px;
    }

    &__item {
      padding-bottom: 40px;
    }

    &__image {
      height: 256px;

      &--1 {
        width: 673px;
      }

      &--2 {
        width: 327px;
      }

      &--5 {
        width: 327px;
      }

      &--6 {
        width: 673px;
      }
    }

    &__caption {
      margin-top: 24px;
    }

    &__button {
      width: 188px;
      margin: 0 auto;
    }
  }
}
